aside {
    position: fixed;
    /* top:8%; */
    bottom:0;
    right:-100%;
    width: 40rem;
    max-width: 34%;
    height: 92vh;
    background: #d6c5c5;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.27);
    transition: right 1.3s ease-out;
    z-index:2;
    overflow: scroll;
    

}

:host([opened]) aside {
    right:0;

}

header{
    display: flex;
    padding: 1rem;
    background: black;
    position: fixed;
    top:8%;
    width:100%;

}
h1.post-title{
    font-size: 1.5rem;
    color: white;
    margin: 0;

}

.post-content{
    margin-left: 1em;
}

h1{
    font-size: 1.5rem;
    margin: 0;

}
header button{
    position: absolute;
    top:0;
    right:0;
    padding: 1rem;
    color:white;
    background: transparent;
    font-size: 1.5rem;
    border: none;

}

header button:focus{
   outline: none; 
}

#tabs{
    display: flex;
    justify-content: center;
    width:100%;
    margin: 1rem 0;
    padding-top: 4rem;

}
#tabs button{
    width: 30%;
    background: white;
    color: black;
    text-align: center;
    border: 1px solid black;
    font: inherit;
    padding: 0.15rem 0;
}
#tabs button.active,
#tabs button:hover,
#tabs button:active{
    background:black;
    color: white
}
#tabs button:focus{
    outline: none;
}
#contact-information{
    padding: 0 1rem;

}
