.site-header,
.site-footer,
.book-header,
.book-summary,
.search-results{
    display: none;
}
ul.nav{
    margin: 1em;
    padding: 0;
    list-style: none;

}
li.nav{
    margin: 0.25rem 0;
    padding:0.25rem;
    border:  1px solid #ccc;
    cursor: pointer;
}

li.nav:hover,
li.nav:active{
    background:black;
    color: white;
}